<template>
  <div class="index">

    <!-- banner -->
    <div class="jumbotron">
      <div class="container">
        <div class="text-center">
          <h1>Vue-Video-Player</h1>
          <h3>Video Player component for Vue.js</h3>
          <br>
          <p>
            <a href="https://github.com/surmon-china/vue-video-player" class="btn btn-primary btn-lg" target="_blank" v-touch-ripple>View on GitHub</a>
            <span>&nbsp;</span>
            <a class="btn btn-success btn-lg" @click="playerAction('play')" v-touch-ripple>play all video</a>
            <span>&nbsp;</span>
            <a class="btn btn-warning btn-lg" @click="playerAction('pause')" v-touch-ripple>pause all video</a>
            <span>&nbsp;</span>
            <a class="btn btn-info btn-lg" @click="playerAction('refresh')" v-touch-ripple>refresh all video</a>
          </p>
        </div>
      </div>
    </div>

    <!-- editor lists -->
    <div class="container">

      <!-- data bind -->
      <div class="row">
        <div class="col-xs-12">
          <div class="panel panel-default">
            <div class="panel-heading" v-touch-ripple>
              <h4 class="text-center">
                <span>Use and code example</span>
              </h4>
            </div>
            <div class="panel-body">
              <div class="row" v-for="video in videoOptions">
                <div class="col-xs-6">
                  <video-player :options="video.option"></video-player>
                </div>
                <div class="col-xs-6">
                  <codemirror :code="video.code" :options="exampleEditorOption" class="exampleEditor"></codemirror>
                </div>
                <div class="col-xs-12"><br></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  const exampleEditorOption = {
    tabSize: 2,
    styleActiveLine: true,
    lineNumbers: true,
    line: true,
    mode: 'application/ld+json',
    lineWrapping: false,
    theme: 'base16-dark'
  }

  let videoOptions = {
    1: {
      option: {
        source: {
          type: "video/youtube",
          src: "https://www.youtube.com/watch?v=iD_MyDbP_ZE"
        },
        defaultSrcReId: 'low',
        autoplay: true,
        controls: false,
        ytControls: true
      }
    },
    2: {
      option: {
        source: [{
          type: "video/mp4",
          src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_H',
          label: '原画',
          res: 1
        }, {
          type: "video/mp4",
          src: 'http://221.11.100.42/7xkwa7.media1.z0.glb.clouddn.com/sample_video_M?wsiphost=local',
          label: '高清',
          res: 2
        }, {
          type: "video/mp4",
          src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_L',
          label: '流畅',
          res: 3
        }],
        language: 'zh-CN',
        playbackRates: [0.7, 1.0, 1.3, 1.5, 1.7],
        poster: 'http://www.freemake.com/blog/wp-content/uploads/2015/06/videojs-logo.jpg',
        defaultSrcReId: 2,
        height: 360,
        autoplay: false
      }
    },
    3: {
      option: {
        source: {
          type: 'video/mp4',
          src: 'http://vjs.zencdn.net/v/oceans.mp4'
        },
        techOrder: ['flash'],
        poster: 'http://www.freemake.com/blog/wp-content/uploads/2015/06/videojs-logo.jpg',
        muted: false,
        autoplay: false
      }
    },
    4: {
      option: {
        source: {
          type: 'video/webm',
          src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm',
        },
        playbackRates: [0.5, 1.0, 1.5, 2.0],
        poster: 'http://www.freemake.com/blog/wp-content/uploads/2015/06/videojs-logo.jpg',
        autoplay: false
      }
    },
    5: {
      option: {
        source: {
          type: 'video/mp4',
          src: 'http://assets7.ign.com/videos/zencoder/2016/06/15/960/7080c56a76e2b74ec8ecfe4c224441d4-1500000-1466028542-w.mp4',
        },
        poster: 'http://assets1.ignimgs.com/thumbs/userUploaded/2016/6/15/zelda-1466028489388_large.jpg',
        autoplay: false,
        muted: false
      }
    },
    6: {
      option: {
        source: {
          type: 'application/x-mpegURL',
          src: 'https://logos-channel.scaleengine.net/logos-channel/live/biblescreen-ad-free/playlist.m3u8',
          withCredentials: false
        },
        poster: 'data:image/png;base64,',
        live: true,
        autoplay: false
      }
    }
  }

  Object.keys(videoOptions).forEach(index => {
    let options = JSON.stringify(videoOptions[index].option, null, "\t")
    videoOptions[index].code =  `// videoOptionsExample\n// https://github.com/surmon-china/vue-video-player \n${options}`
  })

  export default {
    data () {
      return {
        exampleEditorOption,
        videoOptions
      }
    },
    // 在创建实例时 `events` 选项简单地调用 `$on`
    events: {
      'player': function (msg) {
        // console.log(msg)
      }
    },
    methods: {
      playerAction: function (action) {
        this.$broadcast('player', action)
      }
    }
  }
</script>

<style lang="scss">
  .CodeMirror.cm-s-base16-dark {
    // height: 360px!important;
  }
</style>


